<template>
  <div class="room">
    <el-container>
      <el-aside class="aside" width="240px">
        <div class="scroll scroll1">
          <el-row>
            <player-partial></player-partial>
          </el-row>
        </div>
      </el-aside>
      <el-main class="main">
        <div id="mainVideo">
          <video-partial></video-partial>
        </div>
      </el-main>

      <el-aside width="150px">
        <div class="chatRoom">
          <el-row>
            <div class="textContent">
              <ul v-for="(item, index) in messages" v-bind:key="index + 'itemMessage'">
                <li>
                  <b>Name:</b>
                  {{item.user}}
                </li>
                <li>
                  <b>Message:</b>
                  {{item.message}}
                </li>
              </ul>
            </div>
          </el-row>
          <el-row>
            <el-input type="text" name="Name" id="userName" v-model="userName" placeholder="請輸入內容"></el-input>
            <el-input
              type="text"
              name="Message"
              id="userMessage"
              v-model="userMessage"
              placeholder="請輸入內容"
            ></el-input>
            <el-button round v-on:click="submitCard">
              <strong>Send</strong>
            </el-button>
          </el-row>
        </div>
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import PlayerPartial from "@/components/PlayerPartial.vue";
import VideoPartial from "@/components/VideoPartial.vue";
import * as signalr from "@aspnet/signalr";

export default {
  name: "room",
  data() {
    return {
      userName: "",
      userMessage: "",
      messages: [],
      signalrHub: null,
      isCountdown: true,
      times:10
    };
  },
  methods: {
    submitCard: function() {
      if (this.userName && this.userMessage) {
        this.signalrHub = new signalr.HubConnectionBuilder()
          .invoke("SendMessage", this.userName, this.userMessage)
          .catch(function(err) {
            return console.error(err.toSting());
          });

        this.userName = "";
        this.userMessage = "";
      }
    }
  },
  created: function() {
    this.signalrHub = new signalr.HubConnectionBuilder()
      .withUrl("https://localhost:5001/chatHub")
      .configureLogging(signalr.LogLevel.Information)
      .build()
      .start()
      .catch(function(err) {
        return console.error(err.toSting());
      });
  },

  mounted() {
    this.signalrHub = new signalr.HubConnectionBuilder()
      .withUrl("https://localhost:5001/chatHub")
      .build()
      .start()

      .then(res => {
        this.signalrHub.on("ReceiveMessage", function(user, message) {
          this.messages.push({ user, message });
        });
        this.signalrHub.invoke("");
      });
  },
  components: {
    PlayerPartial,
    VideoPartial
  }
};
</script>

<style lang='css' scoped>
.room {
  height: 100%;
  justify-content: center;
}

/* .el-container{
 
  align-items: center;
} */

.el-main {
  /* height: 440px; */
  padding: 0px;
}

#mainVideo {
  border-radius: 5px;
  /* background-color: #5f98c6; */
}

.aside {
  height: 380px;
  border-radius: 5px;
  padding: 10px;
  background-color: #78bec5;
  overflow: hidden;
}

.el-row {
  margin-bottom: 20px;
}
.scroll {
  width: 200px;
  height: 380px;
  overflow: auto;
  float: left;
  margin: 0 10px;
}
.scroll1::-webkit-scrollbar {
  width: 5px;
}

.scroll1::-webkit-scrollbar-thumb {
  background: gainsboro;
}
.chatRoom {
  width: 150px;
  height: 380px;
  background-color: #78bec5;
  border-radius: 5px;
}

.el-input {
  /* position: relative; */
  width: 120px;
  /* margin-top: 330px; */
}

.textContent {
  height: 220px;
}

</style>